<template>
  <div class="order">
    <!-- 我的订单头部 -->
    <div class="order-header">
      <div class="order-header-content">
        <p>
          <i class="el-icon-s-order" style="font-size: 30px;color: #ff6700;"></i>
          我的订单
        </p>
      </div>
    </div>
    <!-- 我的订单头部END -->

    <!-- 我的订单主要内容 -->
    <div class="order-content" v-if="orders!==null">
      <div class="content" v-for="(item,index) in orders" :key="index">
        <ul>
          <!-- 我的订单表头 -->
          <li class="order-info">
            <div class="order-id">订单编号: {{orders[index].orderItemId}}</div>
            <div class="order-time">订单时间: {{orders[index].createTime | dateFormat}}</div>
            
          </li>
          <li class="header">
            <div class="pro-img"></div>
            <div class="pro-name">商品名称</div>
            <div class="pro-price">单价</div>
            <div class="pro-num">数量</div>
            <div class="pro-total">小计</div>
            <div class="pro-operate">操作</div>
 
          </li>
          <!-- 我的订单表头END -->

          <!-- 订单列表 -->
          <li class="product-list"  >
            <div class="pro-img">
              <router-link :to="{ path: '/goods/details', query: {productID:orders[index].goodsId} }">
                <img :src="orders.goodsCoverImg" />
              </router-link>
            </div>
            <div class="pro-name">
              <router-link
                :to="{ path: '/goods/details', query: {productID:orders[index].goodsId} }"
              >{{orders[index].goodsName}}</router-link>
            </div>
            <div class="pro-price">{{orders[index].sellingPrice}}元</div>
            <div class="pro-num">{{orders[index].goodsCount}}</div>
            <div class="pro-total pro-total-in">{{orders[index].sellingPrice*orders[index].goodsCount}}元</div>
          
            <div class="pro-operate">
              <el-button type="primary" size="mini" v-show="orders[index].orderStatus == 0" @click="delOrder(orders[index].orderItemId)">删除</el-button>
              <el-button type="success" size="mini" v-show="orders[index].orderStatus == 0" @click="undateOrder(orders[index].orderItemId,1)">支付</el-button>
              <el-button type="warning" size="mini" v-show="orders[index].orderStatus == 1" @click="undateOrder(orders[index].orderItemId,0)">退款</el-button>
              <el-button type="danger" size="mini" v-show="orders[index].orderStatus == 2" @click="undateOrder(orders[index].orderItemId,3)">确认签收</el-button>
            </div>
    </li>
        </ul>
          <div class="order-bar">
            <!-- <div class="order-bar-left">
              <span class="order-total">
                共
                <span class="order-total-num">{{total[index].totalNum}}</span> 件商品
              </span>
            </div> -->
            <div class="order-bar-right">
              <span>
                <span class="total-price-title">状态：</span>
                <span class="total-price">{{orderst[orders[index].orderStatus]}}  </span>
              </span>
              <br>
              
            </div>
          </div>
 
          <!-- 订单列表END -->
        </div>
      </div>
      <div style="margin-top:-40px;"></div>
    </div>
    <!-- 我的订单主要内容END -->

    <!-- 订单为空的时候显示的内容 -->
    <!-- <div v-else class="order-empty">
      <div class="empty">
        <h2>您的订单还是空的！</h2>
        <p>快去购物吧！</p>
      </div>
    </div> -->
    <!-- 订单为空的时候显示的内容END -->
  <!-- </div> -->
</template>
<script>
import { mapGetters } from "vuex";

export default {
    computed: {
    ...mapGetters([
      "getOrder",
    ]),
  },
  // 0待支付 1已支付 2已发货 3已签收 
  data() {
    
    return {
      orderst:["待支付","已支付","已发货","已签收"],
      forPay:true,
      orders: [], // 订单列表
      total: [] // 每个订单的商品数量及总价列表
    };
  },
  activated() {
     
  },
  created(){
    this.indexorder();
  },
  watch: {
 
  },
  methods:{
    undateOrder(id ,val){
       this.$axios(   
            {
            //  url: "http://localhost:3000/cart", 
           url: this.$target+ "/updateOrder",
              method: 'post',
              params:{
                orderItemId:id ,
                status:val ,
              }
        })
        .then(res => {
          if(res.data.flag){
                this.notifySucceed(res.data.msg);
            }
            else{
              this.notifyError("更新订单失败",res.data.msg);
            }
          })
        .catch(err => {
          return Promise.reject(err);
        });  this.$router.go(0)
    },
    delOrder(val){
      let as = this.$store.getters.getUserId;
      this.$axios(   
            {
            //  url: "http://localhost:3000/cart", 
           url: this.$target+ "/delOrder",
              method: 'psot',
              params:{
                orderItemId:val,
                userId:as

              }
        })
        .then(res => {
          if(res.data.flag){
               console.log(this.orders);
               this.notifySucceed(res.data.msg);
            }
            else{
              this.notifyError("删除失败",res.data.msg);
            }
          })
        .catch(err => {
          return Promise.reject(err);
        }); 
        this.$router.go(0)

    },
    indexorder(){
    let as = this.$store.getters.getUserId;
    this.$axios(   
            {
            //  url: "http://localhost:3000/cart", 
           url: this.$target+ "/orders",
              method: 'get',
              params:{
                userId:as
              }
        })
        .then(res => {
          if(res.data.flag){
              this.orders = res.data.data;
              console.log(this.orders);
            }
            else{
              this.notifyError("获取购物车的所有商品",res.data.msg);
            }
          })
        .catch(err => {
          return Promise.reject(err);
        }); 
    }

  }
};
</script>
<style scoped>
.order {
  background-color: #f5f5f5;
  padding-bottom: 20px;
}
/* 我的订单头部CSS */
.order .order-header {
  height: 64px;
  border-bottom: 2px solid #ff6700;
  background-color: #fff;
  margin-bottom: 20px;
}
.order .order-header .order-header-content {
  width: 1225px;
  margin: 0 auto;
}
.order .order-header p {
  font-size: 28px;
  line-height: 58px;
  float: left;
  font-weight: normal;
  color: #424242;
}
/* 我的订单头部CSS END */
.order .content {
  width: 1225px;
  margin: 0 auto;
  background-color: #fff;
  margin-bottom: 50px;
}

.order .content ul {
  background-color: #fff;
  color: #424242;
  line-height: 85px;
}
/* 我的订单表头CSS */
.order .content ul .order-info {
  height: 60px;
  line-height: 60px;
  padding: 0 26px;
  color: #424242;
  border-bottom: 1px solid #ff6700;
}
.order .content ul .order-info .order-id {
  float: left;
  color: #ff6700;
}
.order .content ul .order-info .order-time {
  float: right;
}

.order .content ul .header {
  height: 85px;
  padding-right: 26px;
  color: #424242;
}
/* 我的订单表头CSS END */

/* 订单列表CSS */
.order .content ul .product-list {
  height: 85px;
  padding: 15px 26px 15px 0;
  border-top: 1px solid #e0e0e0;
}
.order .content ul .pro-img {
  float: left;
  height: 85px;
  width: 120px;
  padding-left: 80px;
}
.order .content ul .pro-img img {
  height: 80px;
  width: 80px;
}
.order .content ul .pro-name {
  float: left;
  width: 200px;
}
.order .content ul .pro-name a {
  color: #424242;
}
.order .content ul .pro-name a:hover {
  color: #ff6700;
}
.order .content ul .pro-price {
  float: left;
  width: 50px;
  padding-right: 18px;
  text-align: center;
}
.order .content ul .pro-operate {
  float: left;
  width: 200px;
  padding-left: 100px;
  text-align: center;
}
.order .content ul .pro-num {
  float: left;
  width: 190px;
  text-align: center;
}
.order .content ul .pro-total {
  float: left;
  width: 160px;
  padding-right: 81px;
  text-align: right;
}
.order .content ul .pro-total-in {
  color: #ff6700;
}

.order .order-bar {
  width: 1185px;
  padding: 0 20px;
  border-top: 1px solid #ff6700;
  height: 50px;
  line-height: 50px;
  background-color: #fff;
}
.order .order-bar .order-bar-left {
  float: left;
}
.order .order-bar .order-bar-left .order-total {
  color: #757575;
}
.order .order-bar .order-bar-left .order-total-num {
  color: #ff6700;
}
.order .order-bar .order-bar-right {
  float: right;
}
.order .order-bar .order-bar-right .total-price-title {
  color: #ff6700;
  font-size: 14px;
}
.order .order-bar .order-bar-right .total-price {
  color: #ff6700;
  font-size: 30px;
}
/* 订单列表CSS END */

/* 订单为空的时候显示的内容CSS */
.order .order-empty {
  width: 1225px;
  margin: 0 auto;
}
.order .order-empty .empty {
  height: 300px;
  padding: 0 0 130px 558px;
  margin: 65px 0 0;
  background: url(../assets/imgs/cart-empty.png) no-repeat 124px 0;
  color: #b0b0b0;
  overflow: hidden;
}

.order .order-empty .empty h2 {
  margin: 70px 0 15px;
  font-size: 36px;
}
.order .order-empty .empty p {
  margin: 0 0 20px;
  font-size: 20px;
}
/* 订单为空的时候显示的内容CSS END */
</style>